import AssetActionPlugin from "../AssetActionPlugin";
import URI from 'urijs';

export default class TerrainViewer extends AssetActionPlugin {

  get show() {
    // Disabled - "View in 3D" button is now shown in the header via StacHeader.vue
    // This avoids duplicate buttons for DEM assets
    return false;
  }

  get uri() {
    // Use the local terrain viewer
    let uri = new URI("https://stac.uixai.org/terrain/index.html");
    uri.addQuery('dem', this.component.href);
    return uri;
  }

  get text() {
    return "View in 3D";
  }

}
