import AssetActionPlugin from "../AssetActionPlugin";
import URI from 'urijs';

export default class PotreeViewer extends AssetActionPlugin {

  get show() {
    // Show button only for COPC point cloud files
    return this.component.isBrowserProtocol && this.asset.type === 'application/vnd.laszip+copc';
  }

  get uri() {
    // Use the local Potree viewer (supports RGB colors)
    let uri = new URI("https://stac.uixai.org/potree/index.html");
    uri.addQuery('files', this.component.href);
    return uri;
  }

  get text() {
    return "View in Potree";
  }

}
